
@import url(./assets/css/iview/iview.css);
@import url(./assets/css/atom-one-light.css);
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
  background: transparent;
}
body {
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
  -moz-tap-highlight-color: transparent;
  -ms-tap-highlight-color: transparent;
  tap-highlight-color: transparent;
  font-family: "-apple-system,'Helvetica Neue',\5FAE\8F6F\96C5\9ED1,'Tohoma'";
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none;
}
:focus {
  outline: 0;
}
ins {
  text-decoration: none;
}
del {
  text-decoration: line-through;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
caption, th, td {
  text-align: left;
  font-weight: normal;
  float: none !important;
}
table, th, td {
  vertical-align: middle;
}
em, i {
  font-style: normal;
}
a img {
  border: none;
  text-decoration: none;
}
:focus {
  outline: 0;
}
.clearfix:before, .clearfix:after {
  content: "";
  display: table;
  font-size: 0 !important;
  line-height: 0 !important;
  height: 0 !important;
}
.clearfix:after {
  clear: both;
}
.clearfix {
  *zoom: 1;
}
.clear {
  clear: both;
  display: block;
  overflow: hidden;
  visibility: hidden;
  width: 0;
  height: 0;
}
input {
  -webkit-user-modify: read-write-plaintext-only;
}
input[type="reset"]::-moz-focus-inner,
input[type="button"]::-moz-focus-inner,
input[type="submit"]::-moz-focus-inner,
input[type="file"] > input[type="button"]::-moz-focus-inner {
  border: none;
  padding: 0;
}
html {
  font-size: 20vw;
  height: 100%;
  width: 100%;
}
@media screen and (max-width: 320px) {
html {
      font-size: 64px;
}
}
@media screen and (min-width: 540px) {
html {
      font-size: 108px;
}
}
body {
  height: 100%;
}
.layout {
  min-height: 100%;
  background: #f5f7f9;
}
.main-width {
  position: relative;
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
}
.layout-top {
  height: 100%;
}
.layout-logo {
  position: relative;
  width: 140px;
  height: 100%;
  float: left;
}
.layout-logo a {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
}
.layout-logo a img {
      position: absolute;
      width: 100px;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      margin: auto;
}
.layout-nav {
  height: 60px;
  margin: 0 auto;
}
.layout-nav a {
    color: inherit;
}
.layout-assistant {
  width: 300px;
  margin: 0 auto;
  height: inherit;
}
.layout-breadcrumb {
  padding: 10px 15px 0;
}
.layout-content {
  min-height: 460px;
  margin: 15px;
  overflow: hidden;
  background: #fff;
  border-radius: 4px;
}
.layout-content-main {
  padding: 10px;
}
.layout-copy {
  text-align: center;
  padding: 10px 0 20px;
  color: #9ea7b4;
}
.bl-list li {
  font-size: 0.13333rem;
  line-height: 0.26667rem;
}
.bl-list li span {
    float: right;
}
.bl-about h3 {
  font-size: 0.18667rem;
  line-height: 1.8;
}
.bl-about p {
  font-size: 0.13333rem;
  line-height: 1.6;
}
.markdown-content {
  font-size: 0.13333rem;
  line-height: 1.4;
}
.markdown-content h2 {
    margin-bottom: 0.10667rem;
}
.markdown-content code {
    padding: 0.10667rem;
    margin: 0.05333rem 0 0.10667rem;
    display: block;
    background: #f7f7f7;
    max-height: 6rem;
    overflow: auto;
    font-size: 0.10667rem;
}
.markdown-content a {
    color: #42b983;
    font-weight: 600;
}
.markdown-content blockquote {
    margin: 8px 0 15px;
    padding-left: 10px;
    border-left: 4px solid #42b983;
    background: #f7f7f7;
}
.markdown-content blockquote p {
      font-weight: 600;
      margin-left: 0;
      margin-bottom: 0;
      padding-bottom: 0;
}
.markdown-content blockquote li {
      list-style: disc inside;
}
.markdown-content blockquote strong {
      color: #42b983;
}
.markdown-content p {
    margin-bottom: 10px;
}
.markdown-content p > img {
    max-width: 95%;
}
.markdown-content table {
    margin: 0.06667rem 0;
}
.markdown-content table th,
    .markdown-content table td {
      padding-left: 0.13333rem;
      padding-right: 0.13333rem;
      min-width: 0;
      height: 0.4rem;
      box-sizing: border-box;
      text-align: left;
      text-overflow: ellipsis;
      vertical-align: middle;
      border-bottom: 1px solid #e9eaec;
}
.markdown-content table th {
      white-space: nowrap;
      overflow: hidden;
      background-color: #f8f8f9;
}



